body::before {
  content: '';
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
  background-repeat: no-repeat;
  --grid-sides: 16px;
  --grid-gutter: 16px;
  --grid-colums: 4;
  --grid-color: rgba(255, 0, 0, 0.1);
  --grid-column-width: calc((100% - var(--grid-gutter) * var(--grid-colums)) / var(--grid-colums));
  border-left: calc(var(--grid-sides) - var(--grid-gutter) / 2) solid transparent;
  border-right: calc(var(--grid-sides) - var(--grid-gutter) / 2) solid transparent;
  background-image: repeating-linear-gradient(to right, transparent 0, transparent calc(var(--grid-gutter) / 2), var(--grid-color) calc(var(--grid-gutter) / 2), var(--grid-color) calc(var(--grid-column-width) + var(--grid-gutter) / 2), transparent calc(var(--grid-column-width) + var(--grid-gutter) / 2), transparent calc(var(--grid-column-width) + var(--grid-gutter)));
}
@media (min-width: 768px) {
  body::before {
    --grid-sides: 24px;
    --grid-colums: 8;
  }
}
body {
  margin: 0;
  padding: 0;
  background-image: url(../img/1.jpg);
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}
